.input{
    display: block;
    margin: 10px 0;
}
.button {
    background-color: blue;
    color: white;
    border: 2px solid white;
    padding: 6px 12px;
    border-radius: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
  cursor: pointer;
  }
  
  .button:hover {
      background-color: #e7e7e7;
      color: black
    }
    